import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { suggest } from '../../funnelback';
import Clear from './finder__clear';

const maximumSuggestions = 5,
    [keyCodeEscape, keyCodeUp, keyCodeDown] = [27, 38, 40],
    body = document.getElementsByTagName('body'),
    backgroundFill = body[0].querySelectorAll('.background-fill');
let queryParam;

/**
 * Search input field and autocomplete.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__query(props) {
    // save what they're typing
    const [partialQuery, setPartialQuery] = useState(props.query.query);
    // Funnelback suggestions for the currently typed text
    const [suggestions, setSuggestions] = useState([]);
    // Request token for calls to the Funnelback suggestions service, so we
    // can cancel it
    const [call, setCall] = useState({ cancel: () => {} });
    // ref for the input field, so we can .focus() it
    const [inputId] = useState(
        'finder--' +
            props.query.collection +
            '--' +
            Math.random()
                .toString(16)
                .slice(-4)
    );

    useEffect(() => {
        setPartialQuery(props.query.query);
    }, [props.updating]);

    let textInput = null;
    function focusInput() {
        textInput.focus();
    }

    // on clear, make a default request for results
    const clearQuery = () => {
        call.cancel();
        setPartialQuery('');
        setSuggestions([]);
        focusInput();

        const newQuery = props.query;
        newQuery.query = '';
        newQuery.sortBy = props.config.sort;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const submitForm = () => {
        call.cancel();
        setSuggestions([]);

        const newQuery = props.query;
        newQuery.query = partialQuery ? partialQuery : '';
        newQuery.startRank = 1;
        newQuery.sortBy = partialQuery ? null : props.config.sort;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const submitSuggestion = s => {
        call.cancel();
        const newQuery = props.query;
        newQuery.query = s;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    // render suggestions
    // TODO: probably should be refactored into a separate component
    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul className="finder__query__suggestions">
            {[...new Set(suggestions)]
                .slice(0, maximumSuggestions)
                .map(suggestion => (
                    <li key={suggestion}>
                        <button
                            type="button"
                            onClick={() => {
                                setPartialQuery(suggestion);
                                setSuggestions([]);
                                focusInput();
                                submitSuggestion(suggestion); //passing suggestion because partialQuery value get overwritten for some reason.
                            }}
                            onKeyDown={e => {
                                switch (e.keyCode) {
                                    case keyCodeEscape:
                                        e.target.parentNode.parentNode.parentNode
                                            .querySelector('input')
                                            .focus();
                                        setSuggestions([]);
                                        break;
                                    case keyCodeUp:
                                        if (
                                            e.target.parentNode
                                                .previousElementSibling &&
                                            e.target.parentNode.previousElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.previousElementSibling
                                                .querySelector('button')
                                                .focus();
                                        } else {
                                            e.preventDefault();
                                            e.target.parentNode.parentNode.parentNode
                                                .querySelector('input')
                                                .focus();
                                        }
                                        break;
                                    case keyCodeDown:
                                        e.preventDefault();
                                        if (
                                            e.target.parentNode
                                                .nextElementSibling &&
                                            e.target.parentNode.nextElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.nextElementSibling
                                                .querySelector('button')
                                                .focus();
                                        }
                                        break;
                                }
                            }}
                            dangerouslySetInnerHTML={{
                                __html: suggestion.replace(
                                    partialQuery,
                                    `<strong>${partialQuery}</strong>`
                                ),
                            }}
                        ></button>
                    </li>
                ))}
        </ul>
    );

    const input = (
        <div className="finder__query__input">
            <div className="finder__query__icon--wrapper">
                <span className="finder__icon fal fa-search"></span>
            </div>
            <label className="sr-only" htmlFor={inputId}>
                {`Search ${props.config.summariseAs.plural}`}
            </label>
            <input
                autoComplete="off"
                id={inputId}
                name="query"
                placeholder={props.config.placeholder}
                ref={input => {
                    textInput = input;
                }}
                type="text"
                value={partialQuery}
                onKeyDown={e => {
                    switch (e.keyCode) {
                        case keyCodeEscape:
                            clearQuery();
                            break;
                        case keyCodeDown:
                            if (suggestions && suggestions.length > 0) {
                                e.preventDefault();
                                e.target.parentNode
                                    .querySelector(
                                        '.finder__query__suggestions button'
                                    )
                                    .focus();
                            }
                            break;
                    }
                }}
                onBlur={e => {
                    backgroundFill[0].classList.remove('background-fill--grey');
                    e.target.placeholder = props.config.placeholder;
                    e.target.value = '';
                    let suggestedResults = document.querySelector(
                        '.finder__query__suggestions'
                    );
                    document.addEventListener('click', function(event) {
                        let classList = event.target.classList;
                        if (classList.contains('finder__query__suggestions')) {
                            suggestedResults.style.display = 'block';
                        } else {
                            suggestedResults.style.display = 'none';
                        }
                    });
                }}
                onFocus={e => {
                    backgroundFill[0].classList.add('background-fill--grey');
                    e.target.placeholder = '';
                }}
                onChange={e => {
                    //clear old suggestions
                    setSuggestions([]);
                    // keep  what they're typing
                    setPartialQuery(e.target.value);

                    /**
                     * if we have a request to the suggestions service in progress,
                     * cancel it. Stops old suggestions overwriting new ones because
                     * the requests can't keep up with fast typing.
                     */
                    call.cancel();

                    if (e.target.value) {
                        // input is populated, ask for suggestions
                        const [suggestionsPromise, newCall] = suggest(
                            props.query.collection,
                            e.target.value
                        );

                        // update our request cancel function for the new request
                        setCall({
                            cancel: () => {
                                newCall.cancel();
                            },
                        });

                        suggestionsPromise
                            .then(data => setSuggestions(data))
                            .catch(() => setSuggestions([]));
                    } else {
                        // input is empty, empty suggestions
                        setSuggestions([]);
                    }

                    // Build query param to pass to listing page
                    let queryValue = document.getElementById(inputId);
                    if (queryValue) {
                        queryParam = queryValue.value
                            .replace(/\s+/g, '+')
                            .toLowerCase();

                        if (queryParam) {
                            queryParam = `query=${queryParam}`;
                        } else {
                            queryParam = null;
                        }
                    }
                }}
            />
            {suggestionsList}
            {clear}
        </div>
    );

    let hub = document.getElementsByClassName('wrapper--finder--hub');
    hub = hub[0];
    if (hub) {
        /**
         * Build URL taking users from content hub to listing page with selected results.
         */
        const finderRedirect = () => {
            let levelParam;
            let levelSelect = document.getElementById('level-select');
            if (levelSelect) {
                levelParam = levelSelect.value
                    .replace(/\s+/g, '-')
                    .toLowerCase();
                if (levelParam !== 'all-levels') {
                    levelParam = `meta_L_orsand=${levelParam}`;
                } else {
                    levelParam = null;
                }
            }

            // Look to do switch statement later
            const listingUrl =
                'https://web2020.city.ac.uk/prototype/prospective-students/courses';
            if (levelParam && queryParam) {
                window.location.href = `${listingUrl}?${levelParam}&${queryParam}`;
            } else if (levelParam && !queryParam) {
                window.location.href = `${listingUrl}?${levelParam}`;
            } else if (!levelParam && queryParam) {
                window.location.href = `${listingUrl}?${queryParam}`;
            } else {
                window.location.href = `${listingUrl}`;
            }
        };

        return (
            <fieldset className="finder__query">
                <div>
                    {input}
                    <button
                        type="submit"
                        className="finder__query__submit finder__query__submit--redirect"
                        onClick={() => finderRedirect()}
                    >
                        <span
                            className="fal fa-search finder__query__submit__icon"
                            aria-hidden="true"
                        />{' '}
                        <span className="finder__query__submit__text">
                            Find
                        </span>
                    </button>
                </div>
            </fieldset>
        );
    }

    return (
        <fieldset className="finder__query">
            <div>
                {input}
                <button
                    type="submit"
                    className="finder__query__submit"
                    onClick={() => submitForm()}
                >
                    <span
                        className="fal fa-search finder__query__submit__icon"
                        aria-hidden="true"
                    />{' '}
                    <span className="finder__query__submit__text">Find</span>
                </button>
            </div>
        </fieldset>
    );
}

finder__query.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default finder__query;
